* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    scroll-behavior: smooth;
    outline: 0px solid red;
}

.App {
    height: 100vh;
    width: 100vw;
    background-color: #009396;
    overflow-x: hidden;
}


/* Track */

::-webkit-scrollbar-track {
    background: #f1f1f100;
}


/* width */

::-webkit-scrollbar {
    width: 0px;
}

.signup-body img {
    width: 35%;
    margin-left: 10%;
}


/* forms sign up */

.form-maindiv form {
    background-color: #FEFFFF;
    padding: 5vh;
    box-shadow: 40px 40px #015D3C;
    border-radius: 5%;
}

.form-maindiv button {
    width: 100%;
    font-size: 3vh;
    padding: 1%;
    border-radius: 100px;
    border: none;
    background-color: #3AAFA9;
    color: white;
    cursor: pointer;
}

.form-row {
    margin-top: 3%;
}

.form-maindiv a {
    color: #015D3C;
    font-weight: bold;
    cursor: pointer;
    font-size: 1.5vh;
}

.form-row label {
    font-size: 3vh;
    font-family: 'Roboto Slab', serif;
}

.form-row input {
    font-size: 3vh;
    width: 100%;
    padding: 1%;
    font-family: 'Roboto Slab', serif;
    margin-right: 5%;
}

.form-maindiv span {
    font-size: 1.5vh;
    font-family: 'Roboto Slab', serif;
}

.main-content {
    display: flex;
    padding-top: 10%;
    padding-left: 10%;
    padding-right: 10%;
}

.main-content h1 {
    flex: 1;
    color: white;
    font-family: 'Roboto Slab', serif;
    font-size: 10vh;
    margin: 0px 0px;
}

.aLogIn {
    font-family: 'Roboto Slab', serif;
    cursor: pointer;
}

.aLogInMain {
    margin-top: 2%;
}

.main-content .form-maindiv {
    flex: 1;
}

.mainDivCheckbox {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 3% 3%;
}

.mainDivCheckbox input {
    margin-right: 5%;
    font-size: 2vh;
    color: #015D3C;
}

.mainDivCheckbox label {
    font-family: 'Roboto Slab', serif;
    font-size: 1.5vh;
}

.errorSpan {
    color: red;
    margin-left: 1%;
}


/* for modals sign up */

.modalBackgroundClose {
    visibility: hidden;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(58, 175, 169, 0.8);
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalOpen {
    visibility: visible;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(58, 175, 169, 0.8);
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalContainer {
    background-color: #015D3C;
    padding: 1%;
    width: 30%;
    margin-top: 7%;
    border-radius: 15px;
    /* width: 30%;
  margin-top: 10%; */
}

.modalContainer>div {
    margin: 5% 5%;
}

.modalTitleContainer {
    display: flex;
    justify-content: center;
    margin-bottom: 10%;
}

.modalTitle {
    font-family: 'Lobster', cursive;
    font-size: 3.5vh;
    color: white;
}

.modalInfoLabel {
    font-family: 'Roboto Slab', serif;
    font-size: 3vh;
    color: white;
}

.modalInfoInput {
    font-family: 'Roboto Slab', serif;
    font-size: 2vh;
    color: #3AAFA9;
}

.buttonRow {
    margin-top: 10%;
    display: flex;
    flex-direction: column;
}

.buttonRow button {
    margin: 3% 3%;
    font-size: 2vh;
    font-family: 'Roboto Slab', serif;
    padding: 3%;
    border-radius: 20px;
    color: white;
    background-color: #009396;
    cursor: pointer;
}

.buttonRow button:hover {
    background-color: #35b2b4;
}

.buttonRow .close,
.buttonRow .clearLocal {
    background-color: rgb(235, 39, 39);
}

.buttonRow .close:hover,
.buttonRow .clearLocal:hover {
    background-color: rgb(219, 60, 60);
}

.errorSpan1 {
    margin: 1% 1%;
    color: red;
    font-family: 'Roboto Slab', serif;
}

@media screen and (max-width: 1800px) {
    .form-maindiv button {
        font-size: 1.5vh;
    }
    .form-maindiv span {
        font-size: 1.5vh
    }
    .form-row label,
    .form-row input {
        font-size: 2vh;
    }
    .form-maindiv form {
        padding: 4%;
    }
}

@media screen and (max-width: 1121px) {
    .main-content {
        padding-top: 20%;
    }
}

@media screen and (max-width: 1024px) {
    .form-maindiv form .form-row label {
        font-size: 2vh;
    }
    .main-content>h1 {
        font-size: 7vh;
    }
    .form-maindiv form .input1 {
        font-size: 1vh;
    }
    .form-maindiv form {
        padding: 10%;
    }
}

@media screen and (max-width: 900px) {
    .mainDivCheckbox a {
        font-size: 1.5vh;
    }
    .form-maindiv button {
        margin-top: 5%;
        margin-bottom: 5%;
    }

    .modalContainer {
        width: 70%;
    }
}

@media screen and (max-width: 699px) {
    .main-content {
        display: flex;
        flex-direction: column;
    }
    .form-maindiv form {
        margin-top: 10%;
        margin-bottom: 10%;
    }
    .signup-body img {
        width: 60%;
        margin-top: 10%;
        margin-bottom: 10%;
    }
    .main1 {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .signup-body {
        padding: 10%;
    }
    .main-content h1 {
        font-size: 5vh;
    }
    .form-maindiv form {
        background-color: #FEFFFF;
        box-shadow: 20px 20px #015D3C;
        border-radius: 5%;
    }
    .mainDivCheckbox label {
        font-size: 1.5vh;
    }
    .form-maindiv span,
    .form-maindiv a {
        font-size: 1.5vh;
    }
    .form-maindiv button {
        margin: 5% 0%;
    }
}

@media screen and (max-width: 427px) {
    .form-maindiv form {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .modalContainer {
        width: 90%;
    }
}

@media screen and (max-width: 383px) {
    .form-maindiv form {
        background-color: #FEFFFF;
        padding: 5vh;
        box-shadow: 0px 0px;
        border-radius: 5%;
    }
    .form-maindiv form {
        padding: 10%;
    }
    .main-content {
        padding-top: 50%;
    }
    .errorSpan1 {
        font-size: 1.5vh;
    }
}


/* Handle */

::-webkit-scrollbar-thumb {
    background: rgb(0, 98, 62);
    border-radius: 1em;
}